import React from 'react'

const SignUp = () => {

  const { register, handleSubmit, formState: { errors } } = useForm({
    defaultValues: {
        name: '',
        email: '',
        password: '',
        gender: '',
        dob: ''
    }
})

const onSubmit = async (data: any) => {

    const res = await newUser(data); //fetchData('/api/user/newUser', data);

    if (res.data)
        router.replace('/');
    responseToast(res);

}

const today = new Date();
const maxDate = new Date(
    today.getFullYear() - 18,
    today.getMonth(),
    today.getDate()
).toISOString().split("T")[0]

  return (
    <div className="register">
        <form onSubmit={handleSubmit(onSubmit)}>
            <h1 className="heading">Register</h1>
            <div className="box">

                {/* Profile name */}
                <div className="input">
                    <label htmlFor="">Name</label>
                    <input
                        type="text"
                        placeholder="Name"
                        {...register('name', { required: 'Name is required' })}
                    />
                    {errors.name && <small>{errors?.name.message}</small>}
                </div>

                {/* Email */}
                <div className="input">
                    <label htmlFor="">Email</label>
                    <input
                        type="email"
                        placeholder="Email"
                        {...register('email', { required: 'Email is required', pattern: /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/ })}
                    />
                    {errors.email && (errors.email.type === 'required'
                        ? <small>{errors.email.message}</small>
                        : <small>Invalid Email</small>
                    )}
                </div>

                <div className="input">
                    <label>Password</label>
                    <input
                        type="password"
                        placeholder="Password"
                        {...register('password', { required: 'Enter Password', minLength: 6 })}
                    />
                    {errors.password && (errors.password.type === 'required'
                        ? <small>{errors.password.message}</small>
                        : <small>Minimum 6 characters required</small>
                    )}
                </div>

                {/* Gender */}
                <div className="input">
                    <label htmlFor="">Gender</label>
                    <select
                        {...register('gender', { required: 'Gender is required' })}
                    >
                        <option value="">Select Gender</option>
                        <option value="male">Male</option>
                        <option value="female">Female</option>
                    </select>
                    {errors.gender && <small>{errors.gender.message}</small>}
                </div>

                {/* Date of Birth */}
                <div className="input">
                    <label htmlFor="">Date of Birth</label>
                    <input
                        type="date"
                        max={maxDate}
                        {...register('dob', { required: 'Enter valid date', max: maxDate })}
                    />
                    {errors.dob && <small>{errors.dob.message}</small>}
                </div>
            </div>
            <div className="buttons">
                <button className="btn">Sign Up</button>
                <p>
                    You have an account? <Link href='/login' className="link">Login</Link>
                </p>
            </div>
        </form>
    </div>
)
}

export default SignUp;